body {
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#timer {
  background-color: rgba(0, 0, 0, 0.705);
  border: 3px solid rgba(68, 68, 68, 0.61);
  border-radius: 10px;
  width: 300px;
}

#timer p {
  position: absolute;
  margin-left: 120px;
  margin-top: 4px;
  color: rgb(255, 0, 0);
  font-weight: bold;
  animation: glow 1.5s ease-in-out infinite alternate;
}

@keyframes glow {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

#time-left {
  margin: 15px auto;
  font-size: 5rem;
  font-weight: bold;
  color: white;
}

.dual-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 500px;
  margin: auto;
  justify-content: center;
  align-items: center;
}

.dual-container h2 {
  font-size: 35px;
}

.buttons {
  margin: -35px 0 10px;
  display: grid;
  grid-template-columns: 50px 200px 50px;
  justify-items: center;
  align-items: center;
}

.buttons h3 {
  font-size: 30px;
}

.waves-effect {
  box-shadow: transparent;
  transition: box-shadow .03s ease-in;
}

.waves-effect:hover {
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.521);
}

.waves-red, .waves-green {
  margin-top: 15px;
}

.waves-red {
  margin-left: 100px;
}

.waves-red:hover {
  background-color: rgba(255, 0, 0, 0.712);
}

.waves-green {
  margin-right: 100px;
}

.waves-green:hover {
  background-color: rgba(0, 255, 0, 0.63);
}

.waves-light {
  margin: 20px 12px -10px;
  background-color: transparent;
}

.waves-light:hover {
  background-color: rgb(99, 224, 255);
}

@media screen and (max-width: 580px) {
  .dual-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
  }
}